import request from '@/utils/request'

export default {
  postCart (goodsId, goodsNum, goodsSkuId) {
    return request.post('/cart/add', {
      goodsId,
      goodsNum,
      goodsSkuId
    })
  },
  getCart () {
    return request.get('/cart/list')
  },
  updateCart (goodsId, goodsNum, goodsSkuId) {
    return request.post('/cart/update', {
      goodsId,
      goodsNum,
      goodsSkuId
    })
  },
  delCart (cartIds) {
    return request.post('/cart/clear', {
      cartIds
    })
  }

}
