import storge from '@/utils/storge'
export default {
  namespaced: true,
  state () {
    return {
      userInfo: storge.getUserInfo()
    }
  },
  mutations: {
    setUserInfo (state, obj) {
      state.userInfo = obj
      storge.setUserInfo(obj)
    }
  },
  actions: {
    logout (context) {
      context.commit('setUserInfo', {})
      context.commit('cart/setCart', [], { root: true })
    }
  },
  getter: {}

}
