const INFO_KEY = 'home_user_info'
const SEARCH_KEY = 'search_info'
export default {
  getUserInfo () {
    const defaultobj = { token: '', userId: '' }
    const res = localStorage.getItem(INFO_KEY)
    return res ? JSON.parse(res) : defaultobj
  },
  setUserInfo (obj) {
    const res = JSON.stringify(obj)
    localStorage.setItem(INFO_KEY, res)
  },
  removeUserInfo () {
    localStorage.clear()
  },
  getSearchList () {
    const res = localStorage.getItem(SEARCH_KEY)
    return res ? JSON.parse(res) : []
  },
  setSearchList (arr) {
    localStorage.setItem(SEARCH_KEY, JSON.stringify(arr))
  }
}
