import cart from '@/api/cart'
export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    setCart (state, newarr) {
      state.cartList = newarr
    },
    // changeCart (state, { goodsNum, goodsId }) {
    //   const goods = state.cartList.find(item => item.goods_id === goodsId)
    //   goods.goods_Num = goodsNum
    // }
    changeCount (state, { goodsId, goodsNum }) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = goodsNum
    },
    changeChecked (state, goodsId) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
    },
    changeAllChecked (state, flag) {
      state.cartList.forEach(item => { item.isChecked = flag })
    }
    // setTotalCount (state, newCount) {
    //   state.totalCount = newCount
    // }
  },
  actions: {
    async getCart (contxt) {
      const res = await cart.getCart()
      console.log(res)
      res.data.list.forEach(element => {
        element.isChecked = true
      })
      contxt.commit('setCart', res.data.list)
    },
    async changeCart (context, obj) {
      const { goodsNum, goodsId, goodsSkuId } = obj
      context.commit('changeCount', { goodsNum, goodsId })
      const res = await cart.updateCart(goodsId, goodsNum, goodsSkuId)
      console.log(res)
    },
    async delSelect (context) {
      const cartIds = context.getters.selCount.map(item => item.id)
      await cart.delCart(cartIds)
      context.dispatch('getCart')
    }
  },
  getters: {
    setTotal (state) {
      return state.cartList.reduce((sub, item) => sub + item.goods_num, 0)
    },
    setPriceTotal (state) {
      return state.cartList.reduce((sub, item) => item.isChecked
        ? sub + item.goods_num * item.goods.goods_price_min
        : sub + 0
      , 0)
    },
    selCount (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    setProCount (state) {
      const newList = state.cartList.filter(item => item.isChecked)
      return newList.reduce((sub, item) => sub + item.goods_num, 0)
    },
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
